	module.exports = {"data":{"wordpressPost":{"title":"Editors’ Picks 2015: Part Seven","content":"<h2><a href=\"http://bmjopen.bmj.com/content/5/9/e008656.long\"><strong>Polypharmacy and potentially inappropriate prescription of medicines</strong></a></h2>\n<p><a href=\"http://blogs.bmj.com/bmjopen/files/2016/01/P7.jpg\" rel=\"attachment wp-att-938\"><img class=\"alignnone size-medium wp-image-938\" src=\"http://blogs.bmj.com/bmjopen/files/2016/01/P7-300x263.jpg\" alt=\"P7\" width=\"300\" height=\"263\" srcset=\"http://blogs.bmj.com/bmjopen/files/2016/01/P7-300x263.jpg 300w, http://blogs.bmj.com/bmjopen/files/2016/01/P7-768x674.jpg 768w, http://blogs.bmj.com/bmjopen/files/2016/01/P7.jpg 821w\" sizes=\"(max-width: 300px) 100vw, 300px\" /></a></p>\n<p>This week we have been looking at some of our staff editors’ favourite articles from 2015. These papers have covered a variety of important issues in medical research, from the transparency of clinical trials to the publication of negative findings. <a href=\"http://bmjopen.bmj.com/content/5/9/e008656.long\">Our final editors’ pick </a>takes us to Ireland, where Professor <a href=\"http://www.hrbcentreprimarycare.ie/?q=user/2\">Tom Fahey </a>and colleagues from the Royal College of Surgeons in Dublin looked at pharmacy claims data to investigate long-term trends in prescribing of medicines between 1997 and 2012. Their findings included the positive news that prescribing quality had improved across the time period. However, there was a substantial increase in the number of people taking multiple regular medicines (known as ‘polypharmacy’), and this significantly predicted potentially inappropriate prescription of medicines. The authors suggested that patients should be made more aware of the trade-off between taking more medicines to prevent disease and the potential for harm from potentially inappropriate prescribing of drugs, which is associated with taking more medicines.<!--TrendMD v2.4.3--></p>\n"},"site":{"siteMetadata":{"title":"Gatsby Default Starter"}}},"pathContext":{"id":"af35f47b-ca9d-583b-b33d-5a70a3243917"}}

